.book-now-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  font-family: 'Poppins', sans-serif;
}

.book-now-card {
  background-color: var(--white);
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.book-now-header {
  background-color: var(--primary-color);
  color: var(--white);
  padding: 2rem;
  position: relative;
}

.book-now-header-title {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.book-now-header-subtitle {
  font-size: 1rem;
  opacity: 0.8;
}

.book-now-back-button {
  position: absolute;
  top: 1rem;
  right: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--white);
  text-decoration: none;
  font-size: 0.875rem;
  transition: opacity 0.3s ease;
}

.book-now-back-button:hover {
  opacity: 0.8;
}

.book-now-content {
  padding: 2rem;
}

.book-now-room-details {
  display: flex;
  gap: 2rem;
  margin-bottom: 2rem;
}

.book-now-room-image {
  width: 300px;
  height: 200px;
  object-fit: cover;
  border-radius: 0.5rem;
}

.book-now-room-info {
  flex: 1;
}

.book-now-room-title {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.book-now-room-price {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 0.5rem;
}

.book-now-room-status {
  font-size: 0.875rem;
  color: var(--gray);
  margin-bottom: 1rem;
}

.book-now-location-info {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--gray);
}

.book-now-videos {
  margin-bottom: 2rem;
}

.book-now-videos-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.book-now-button {
  background-color: var(--primary-color);
  color: var(--white);
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.book-now-button:hover {
  background-color: #4338CA;
}

.book-now-video-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

.book-now-video-item video {
  width: 100%;
  border-radius: 0.25rem;
}

.book-now-video-description {
  font-size: 0.875rem;
  color: var(--gray);
  margin-top: 0.5rem;
}

.book-now-form {
  background-color: var(--background-color);
  padding: 2rem;
  border-radius: 0.5rem;
}

.book-now-form-title {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
}

.book-now-form-group {
  margin-bottom: 1.5rem;
}

.book-now-label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.book-now-input {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid var(--light-gray);
  border-radius: 0.25rem;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.book-now-input:focus {
  outline: none;
  border-color: var(--primary-color);
}

.book-now-submit-button {
  background-color: var(--primary-color);
  color: var(--white);
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 0.25rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.book-now-submit-button:hover {
  background-color: #4338CA;
}

@media (max-width: 768px) {
  .book-now-room-details {
    flex-direction: column;
  }

  .book-now-room-image {
    width: 100%;
    height: auto;
  }
}

