.view-all-rooms-page {
  padding: 2rem 0;
}

.back-to-home {
  margin-bottom: 1rem;
  display: inline-block;
  padding: 0.5rem 1rem;
  font-size: 0.875rem;
}

.search-form {
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
  background-color: var(--white);
  padding: 1.5rem;
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}

.search-form input,
.search-form select {
  padding: 0.75rem;
  border: 1px solid var(--light-gray);
  border-radius: 0.375rem;
  font-size: 1rem;
}

.search-form button {
  padding: 0.75rem 1.5rem;
  background-color: var(--primary-color);
  color: var(--white);
  border: none;
  border-radius: 0.375rem;
  font-size: 1rem;
  cursor: pointer;
}

.search-form button:hover {
  background-color: #4338CA;
}
