.about-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.about-content {
  flex-grow: 1;
  padding: 4rem 0;
}

.about-content h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 2rem;
  text-align: center;
  color: var(--primary-color);
}

.about-text {
  margin-bottom: 4rem;
}

.about-text h2 {
  font-size: 1.8rem;
  font-weight: 600;
  margin-top: 2rem;
  margin-bottom: 1rem;
  color: var(--primary-color);
}

.about-text p {
  margin-bottom: 1rem;
  color: var(--gray);
}

.about-text ul {
  list-style-type: disc;
  margin-left: 1.5rem;
  color: var(--gray);
}

.how-it-works {
  padding: 4rem 0;
}

.how-it-works h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  text-align: center;
  color: var(--primary-color);
}

.steps-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.step {
  text-align: center;
  padding: 2rem;
  background-color: var(--white);
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.step-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.testimonials {
  padding: 4rem 0;
}

.testimonials h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  text-align: center;
  color: var(--primary-color);
}

.testimonials-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.testimonial-card {
  padding: 2rem;
  background-color: var(--white);
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.testimonial-content {
  font-style: italic;
  margin-bottom: 1.5rem;
}

.testimonial-author h4 {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: var(--primary-color);
}

.testimonial-author p {
  color: var(--gray);
}

.testimonial-rating {
  display: flex;
  gap: 0.25rem;
  margin-top: 1rem;
}

.star-filled {
  fill: var(--primary-color);
  stroke: var(--primary-color);
}

.star-empty {
  fill: none;
  stroke: var(--primary-color);
}

@media (max-width: 768px) {
  .steps-grid,
  .testimonials-grid {
    grid-template-columns: 1fr;
  }
}

